import React from "react";
import { Box, Grid, Typography, Container, Card, Link } from "@mui/material";
import Carousel from "nuka-carousel";
import back from "../../assets/Arrows/back-arrow.svg";
import forward from "../../assets/Arrows/forward-arrow.svg";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import { NextButton, BackButton } from "../../utils/theme.js";

const TeamMobile = (props) => {
  return (
    <Grid container flexDirection="column" justifyContent="center" mb="150px">
      <Typography variant="h2" mb="0">
        MEET OUR TEAM
      </Typography>
      <Typography variant="h4" textAlign="center">
        Some of us are actual Chefs
      </Typography>
      <Container>
        <Carousel
          speed={0}
          wrapAround
          autoplay
          renderCenterLeftControls={({ previousSlide }) => (
            <BackButton onClick={previousSlide}>
              <img src={back} alt="back-button"/>
            </BackButton>
          )}
          renderCenterRightControls={({ nextSlide }) => (
            <NextButton onClick={nextSlide}>
              <img src={forward} alt="next-button"/>
            </NextButton>
          )}
          defaultControlsConfig={{
            pagingDotsStyle: {
              fill: "transparent",
            },
          }}
        >
          {props.team.map((member, index, array) => (
            <Grid
              container
              item
              md={12}
              justifyContent="center"
              alignItems="center"
            >
              {index === 0 ? (
                <Card style={{ marginRight: "-80px" }}>
                  <img
                    src={array[array.length - 1].image}
                    alt={array[array.length - 1].name}
                    width="100%"
                    height="auto"
                  ></img>
                </Card>
              ) : (
                <Card style={{ marginRight: "-80px" }}>
                  <img
                    src={array[index - 1].image}
                    alt={array[index - 1].name}
                    width="100%"
                    height="auto"
                  ></img>
                </Card>
              )}
              <Box
                width="220px"
                height="300px"
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  justifyContent: "space-between",
                  marginTop: "50px",
                }}
              >
                <img src={member.image} width="100%" alt={member.name}></img>
                <Typography variant="h3" textAlign="center" mt="10px">
                  {member.name}
                </Typography>
                <Typography
                  variant="h4"
                  textAlign="center"
                  mt="-2vw"
                  width="80vw"
                >
                  {member.title}
                </Typography>
                <Link
                  target="_blank"
                  href={member.linkedin}
                  style={{ marginLeft: "0vw" }}
                >
                  <LinkedInIcon color="icon" fontSize="large" />
                </Link>
              </Box>
              {index === array.length - 1 ? (
                <Card style={{ marginLeft: "-80px" }}>
                  <img
                    src={array[0].image}
                    alt={array[0].name}
                    width="100%"
                    height="auto"
                  ></img>
                </Card>
              ) : (
                <Card style={{ marginLeft: "-80px" }}>
                  <img
                    src={array[index + 1].image}
                    alt={array[index + 1].name}
                    width="100%"
                    height="auto"
                  ></img>
                </Card>
              )}
            </Grid>
          ))}
        </Carousel>
      </Container>
    </Grid>
  );
};
export default TeamMobile;
