import React, { useState } from "react";
import { Grid, Typography, Avatar } from "@mui/material";
import hoverLinkedin from "../../assets/linkedin-hover.png";
import { TeamButton } from "../../utils/theme.js";

const TeamWeb = (props) => {
  const [linkedinHover, setLinkedinHover] = useState(null);

  const handleHoverIn = (name) => {
    setLinkedinHover(name);
  };

  const handleHoverOut = () => {
    setLinkedinHover(null);
  };

  return (
    <Grid
      container
      flexDirection="column"
      alignItems="center"
      sx={{
        backgroundColor: "secondary.main",
      }}
    >
      <Grid item>
        <Typography variant="h2" mb="-15px">
          MEET OUR TEAM
        </Typography>
        <Typography variant="h4" textAlign="center" mb="100px">
          Some of us are actual Chefs
        </Typography>
      </Grid>
      <Grid
        container
        item
        md={9}
        lg={9}
        spacing={5}
        rowSpacing={10}
        justifyContent="center"
      >
        {props.team.map((member) => (
          <Grid
            container
            item
            md={2.4}
            lg={2.4}
            flexDirection="column"
            justifyContent="flex-start"
            alignItems="center"
          >
            <Avatar
              style={{
                width: "fit-content",
                height: "fit-content",
              }}
            >
              <TeamButton
                onMouseMove={() => handleHoverIn(member.name)}
                onMouseLeave={handleHoverOut}
              >
                <a target="_blank" rel="noreferrer" href={member.linkedin}>
                  {linkedinHover === member.name ? (
                    <img
                      src={hoverLinkedin}
                      width="100%"
                      alt="HovertoLinkedin"
                    ></img>
                  ) : (
                    <img
                      src={member.image}
                      width="100%"
                      alt={member.name}
                    ></img>
                  )}
                </a>
              </TeamButton>
            </Avatar>
            <Grid item>
              <Typography variant="h3" textAlign="center">
                {member.name}
              </Typography>
              <Typography variant="h5" textAlign="center">
                {member.title}
              </Typography>
            </Grid>
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};
export default TeamWeb;
